@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans:200,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body{
  background :    #f4f4f4;
}
.App {
  width :         100%;
  padding :       0;
  margin :        0;
  font-family:    'IBM Plex Sans', sans-serif;
  font-size:      12px;
  font-weight:    400;
  color :         #0e94d7;
}
a:link,
a:visited{
  text-decoration:  none;
  color:            #0e94d7;
  transition :      color 300ms ease-out;
}
a:active,
a:hover{
  color:            #15c2f3;
}

.Header{
  width :         360px;
  margin :        60px auto;
}
  h2{
    font-size:        11px;
    font-weight:      400;
    letter-spacing:   0.1em;
    text-transform:   uppercase;
  }

.Loading{
  display :           block;
  width :             66%;
  margin :            0 auto 0px auto;
  padding-top :       30px;
}
  .Loading p{
    margin-top :        28px;
    font-size :         12px;
    text-transform:     uppercase;
    text-align :        center;
    letter-spacing:     0.2em;
  }
  .Loading svg{
    display:            block;
    width :             200px;
    margin :            0 auto;
  }

ul.Pagination{
  position:           relative;
  width :             66%;
  margin :            30px auto 16px auto;
  display :           -webkit-flex;
  display :           flex;
  -webkit-justify-content:    space-between;
          justify-content:    space-between;
  list-style :        none;
  padding :           16px;
  background:         #ffffff;
  border :            5px solid #ffffff;
  border-radius :     16px;
  box-shadow:         0px 0px 10px 0px rgba(0,0,0,0.2);
}
  ul.Pagination li{
    padding :           10px;
    border-radius :     6px;
    min-width :         20px;
    height :            20px;
    line-height:        20px;
    background :        #eaeaea;
    color :             #0e94d7;
    text-align:         center;
    transition :        all 300ms ease-out;
    display :           block;
  }
    ul.Pagination li span{
    }
    ul.Pagination li span.retrieve{
      margin-left :       6px;
      height :            24px;
      width :             24px;
      float :             right;
    }
    ul.Pagination li:hover{
      cursor :            pointer;
      background :        #0e94d7;
      color :             #ffffff;
    }
    ul.Pagination li.active{
      background :        #0e94d7;
      color :             #ffffff;
    }
    ul.Pagination li.active:hover{
      background :          #19a5f0;
    }
