    position :            absolute;
    display :             block;
    width :               100%;
    text-align:           center;
    font-style :          normal;
    font-size :           10px;
    top:                  -20px;
  }*/

.TableHead{
  width :             66%;
  margin :            32px auto 16px auto;
  display :           -webkit-flex;
  display :           flex;
  -webkit-justify-content:    space-between;
          justify-content:    space-between;
  color :             #0e94d7;
}
.Content{

}
  .SmallBody{
    width :             66%;
    display :           -webkit-flex;
    display :           flex;
    -webkit-justify-content:    space-between;
            justify-content:    space-between;
    -webkit-align-items: center;
            align-items: center;
    margin :            5px auto;
    padding :           16px;
    background:         #eaeaea;
    border :            5px solid #ffffff;
    border-radius :     16px;
    box-shadow:         0px 0px 10px 0px rgba(0,0,0,0.2);
    transition:         all 300ms ease-out;
  }
  .SmallBody:hover{
    cursor :            pointer;
    background:         #ffffff;
    box-shadow:         0px 0px 15px 0px rgba(0,0,0,0.3);
    -webkit-transform:          scale(1.05);
            transform:          scale(1.05);
  }


    .TableHead > span,
    .SmallBody > span {
      width :             20%;
      font-size :         12px;
      letter-spacing:     0.1em;
      text-align :        center;
      text-transform:     uppercase;
    }
    .SmallBody > span.time{
      font-weight :       700;
    }
    .TableHead > span{
      font-size :         2vw;
      font-weight :       200;
    }

    .TableHead > span:first-child,
    .SmallBody > span:first-child{
      text-align :        left;
    }
    .TableHead > span:last-child,
    .SmallBody > span:last-child{
      text-align :        right;
    }

@-webkit-keyframes loseOpacity {
  from { opacity:           1;}
  to   { opacity:           0.7;}
}
@keyframes loseOpacity {
  from { opacity:           1;}
  to   { opacity:           0.7;}
}
@-webkit-keyframes toYellow {
  from { fill:              #ff9300;}
  to   { fill:              #ffc500;}
}
@keyframes toYellow {
  from { fill:              #ff9300;}
  to   { fill:              #ffc500;}
}
@-webkit-keyframes toOrange {
  from { fill:              #ffcf00;}
  to   { fill:              #ffb000;}
}
@keyframes toOrange {
  from { fill:              #ffcf00;}
  to   { fill:              #ffb000;}
}

@-webkit-keyframes glowingText {
  from { color:              #0e94d7;}
  to   { color:              #15c2f3;}
}

@keyframes glowingText {
  from { color:              #0e94d7;}
  to   { color:              #15c2f3;}
}

/* SVG PATH ELEMENTS */
/* PARTS OF THE LOGO */
.logo-light-yellow,
.logo-white,
.logo-brown,
.logo-dark-brown{
  -webkit-animation :       loseOpacity 0.4s ease-in alternate infinite;
          animation :       loseOpacity 0.4s ease-in alternate infinite;
}
.logo-light-yellow,
.logo-brown{
  -webkit-animation-delay :    0.15s;
          animation-delay :    0.15s;
}
.logo-orange{
  -webkit-animation :          toYellow 1.2s ease-in alternate infinite;
          animation :          toYellow 1.2s ease-in alternate infinite;
}
.logo-yellow{
  -webkit-animation :          toOrange 1.2s ease-in alternate infinite;
          animation :          toOrange 1.2s ease-in alternate infinite;
  -webkit-animation-delay :    0.5s;
          animation-delay :    0.5s;
}


.Loading p{
  -webkit-animation :          glowingText 0.3s linear alternate infinite;
          animation :          glowingText 0.3s linear alternate infinite;
}


